

<template>
  <div class="order">
    <loading :active.sync="isLoading"></loading>
    <div class="row mt-4">
      <!--  v-for   -->
      <div class="col-md-4 mb-4" v-for="item in products" :key="item.id">
        <div class="card border-0 shadow-sm">
          <!--  -->
          <div style="height: 150px; background-size: cover; background-position: center"
            :style="{backgroundImage: `url(${item.imageUrl})`}">
          </div>
          <div class="card-body">
            <span class="badge badge-secondary float-right ml-2">{{ item.category }}</span>
            <h5 class="card-title">
              <a href="#" class="text-dark">{{ item.title }}</a>
            </h5>
            <p class="card-text">{{ item.content }}</p>
            <div class="d-flex justify-content-between align-items-baseline">
              <div class="h5" v-if="!item.price">{{ item.origin_price }} </div>
              <del class="h6" v-if="item.price"> {{ item.origin_price }} </del>
              <div class="h5" v-if="item.price"> {{ item.price }} </div>
            </div>
          </div>
          <div class="card-footer d-flex">
            <!--  -->
            <button type="button" class="btn btn-outline-secondary btn-sm" @click="getProduct(item.id)">
              <i class="fas fa-spinner fa-spin" v-if="status.LoadingItem == item.id"></i>
              
            </button>
            <!--  -->
            <button type="button" class="btn btn-outline-danger btn-sm ml-auto"
            @click="addtoCart(item.id)">
              <i class="fas fa-spinner fa-spin" v-if="status.LoadingItem == item.id"></i>
              
            </button>
          </div>
        </div>
      </div>
      <input type="text" v-model="text">
      <div class="text" v-for="(abc, index) in filter" :key="index">{{abc.title}}</div>
    </div>
    <!--  -->
    <div class="modal fade" id="productModal" tabindex="-1" role="dialog"
      aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">{{ product.title }}</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <img :src="product.imageUrl" class="img-fluid" alt="">
            <blockquote class="blockquote mt-3">
              <p class="mb-0">{{ product.content }}</p>
              <footer class="blockquote-footer text-right">{{ product.description }}</footer>
            </blockquote>
            <div class="d-flex justify-content-between align-items-baseline">
              <div class="h4" v-if="!product.price">{{ product.origin_price }} </div>
              <del class="h6" v-if="product.price"> {{ product.origin_price }} </del>
              <div class="h4" v-if="product.price"> {{ product.price }} </div>
            </div>
            <select name="" class="form-control mt-3" v-model="product.num">
              <option :value="num" v-for="num in 10" :key="num">
                 {{num}} {{product.unit}}
              </option>
            </select>
          </div>
          <div class="modal-footer">
            <div class="text-muted text-nowrap mr-3">
               
              <!--  -->
              <strong v-if="product.price">{{ product.num * product.price }}</strong>
              <strong v-if="!product.price">{{ product.num * product.origin_price }}</strong>
              
            </div>
            <button type="button" class="btn btn-primary"
              @click="addtoCart(product.id, product.num)">
              <!-- ID  -->
              <i class="fas fa-spinner fa-spin" v-if="product.id === status.loadingItem"></i>
              
            </button>
          </div>
        </div>
      </div>
    </div>

    <!--  -->
    <div class="my-5 row justify-content-center">
      <div class="my-5 row justify-content-center">
        <!--  -->
        <table class="table">
          <thead>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </thead>
          <tbody>
            <tr v-for="item in cart.carts" :key="item.id" v-if="cart">
              <td class="align-middle">
                <!--  -->
                <button type="button" class="btn btn-outline-danger btn-sm" @click="removeCartItem(item.id)">
                  <i class="far fa-trash-alt"></i>
                </button>
              </td>
              <td class="align-middle">
                {{ item.product.title }}
                <div class="text-success" v-if="item.coupon">
                  
                </div>
              </td>
              <td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
              <td class="align-middle text-right">{{ item.final_total | currencyFilters }}</td>
            </tr>
          </tbody>
          <tfoot>
            <tr>
              <td colspan="3" class="text-right"></td>
              <td class="text-right">{{ cart.total | currencyFilters}}</td>
            </tr>
            <!--   -->
            <tr v-if="cart.final_total !== cart.total" >
              <td colspan="3" class="text-right text-success"></td>
              <td class="text-right text-success">{{ cart.final_total | currencyFilters }}</td>
            </tr>
          </tfoot>
        </table>

        <!--  -->
        <div class="input-group mb-3 input-group-sm">
          <input type="text" class="form-control" v-model="coupon_code" placeholder="">
          <div class="input-group-append">
            <!-- api -->
            <button class="btn btn-outline-secondary" type="button" @click="addCouponCartItem">
              
            </button>
          </div>
        </div>

      </div>
    </div>

    <!--  -->
    <div class="my-5 row justify-content-center">
      <!-- submit  -->
      <form class="col-md-6" @submit.prevent="createOrder">
        <div class="form-group">
          <label for="useremail">Email</label>
          <input type="email" class="form-control" name="email" id="useremail"
            v-model="form.user.email" placeholder=" Email" 
            :class="{ 'is-invalid': errors.has('email') }"
            v-validate="'required'">
          <span class="text-danger">{{ errors.first('email') }}</span>
        </div>
      
        <div class="form-group">
          <label for="username"></label>
          <input type="text" class="form-control" name="name" id="username"
            :class="{ 'is-invalid': errors.has('name') }"
            v-model="form.user.name" placeholder="" v-validate="'required'">
            <!-- :class="{ 'is-invalid': errors.has('name') }"  -->
            <!-- v-validate="'required'"  -->
            <!-- errors.has(inputname)  true -->
          <span class="text-danger" v-if="errors.has('name')"></span>
        </div>
      
        <div class="form-group">
          <label for="usertel"></label>
          <input type="tel" 
            class="form-control" 
            id="usertel" 
            name="usertel"
            v-model="form.user.tel" 
            placeholder=""
            v-validate="'required'"
            :class="{ 'is-invalid': errors.has('usertel') }">
            <span class="text-danger" v-if="errors.has('usertel')"></span>
        </div>
      
        <div class="form-group">
          <label for="useraddress"></label>
          <input type="address" 
            class="form-control" 
            name="address" 
            id="useraddress" 
            v-model="form.user.address"
            :class="{ 'is-invalid': errors.has('address') }"
            v-validate="'required'"
            placeholder="">
          <span class="text-danger" v-if="errors.has('address')"></span>
        </div>
      
        <div class="form-group">
          <label for="useraddress"></label>
          <textarea name="" id="" class="form-control" cols="30" rows="10" v-model="form.message"></textarea>
        </div>
        <div class="text-right">
          <button class="btn btn-danger"></button>
        </div>
      </form>
    </div>

  </div>
</template>

<script>
import $ from 'jquery';
export default {
  data(){
    return{
      text:'',
      // 
      products:[],
      // 
      product:[],
      // 
      isLoading:false,
      // 
      status:{
        LoadingItem:" ",
      },
      //  
      cart:[],
      // 
      coupon_code:'',
      // 
      form:{
        user:{
          name:" ",
          email:" ",
          tel: " ",
          address: " ",
        },
        message:" ",
      }
    }
  },
  computed:{
    filter(){
      let text = this.text;
      let filtered = this.products;
      if(text){
        filtered = filtered.filter(function(item){
            return Object.keys(item).some(function(key){
              return String(item[key]).toLowerCase().indexOf(text) > -1
            })
          })
          console.log(filtered);
          
      }
      return filtered;
    }
  },
  methods:{
    // 
    getProducts(){
      const vm = this;
      // API
      const api = `${ process.env.APIPATH }/api/${ process.env.CUSTOMPATH }/products`;
      // getProductsisLoading
      vm.isLoading = true;
      this.$http.get(api).then((response) => {
        //  products products
        vm.products = response.data.products;
        console.log(vm.products)
        // APIisLoading
        vm.isLoading = false;
        // APIpagination  pagination 
      });
    },
    // 
    getProduct(id){
      const vm = this;
      const api = `${ process.env.APIPATH }/api/${ process.env.CUSTOMPATH }/product/${id}`;
      // ID
      vm.status.LoadingItem = id;
      this.$http.get(api).then((response)=>{
        vm.product = response.data.product;
        console.log(vm.product);
        vm.status.LoadingItem = "";
        $('#productModal').modal('show');
      })
    },
    //
    addtoCart(id, qty = 1){
      // 
      const cart = {
        // ID
        product_id: id,
        // 
        qty,
      };
      const vm = this;
      vm.status.LoadingItem = id;
      const api = `${ process.env.APIPATH }/api/${ process.env.CUSTOMPATH }/cart`;
      //  POST API
      this.$http.post(api, { data:cart }).then((response)=>{
          vm.status.LoadingItem = "";
          vm.getCart();
          $('#productModal').modal('hide');
          // console.log(response)
      });
    },
    // 
    getCart(){
      const vm = this;
      const api = `${ process.env.APIPATH }/api/${ process.env.CUSTOMPATH }/cart`;
      // GET
      this.$http.get(api).then((response)=>{
        //  cart 
        vm.cart = response.data.data;
        // console.log(vm.cart)
      })
    },
    // 
    removeCartItem(id){
      const vm = this;
      vm.isLoading = true;
      //  ID 
      const api = `${ process.env.APIPATH }/api/${ process.env.CUSTOMPATH }/cart/${id}`;
      // delete
      this.$http.delete(api).then((response)=>{
        // console.log(response)
        this.getCart();
        vm.isLoading = false;
      })
    },
    //
    addCouponCartItem(){
      const vm = this;
      // API
      const coupon = {
        code: vm.coupon_code,
      }
      const api = `${ process.env.APIPATH }/api/${ process.env.CUSTOMPATH }/coupon`;
      // post coupon_codeAPI
      this.$http.post(api, { data: coupon}).then((response)=>{
        // console.log(response)
        this.getCart();
      })
    },
    //
    createOrder(){
      const vm = this;
      const api = `${ process.env.APIPATH }/api/${ process.env.CUSTOMPATH }/order`;
      // validate  
      this.$validator.validate().then( (result) => {
        if (result) {
          // post vm.formAPI
          this.$http.post(api, { data: vm.form }).then((response)=>{
            if(response.data.success){
              // post   
              vm.$router.push(`/custom_checkorder/${response.data.orderId}`)
            }
            console.log(response)
          })
        } else {
          console.log('')
        }
      });
    }
  },
  created(){
    this.getProducts();
    this.getCart();
  }
}
</script>

<style lang="scss" scoped>
.order{
  >.row{
    >.text{
      width: 100%;
      display: block;
    }
  }
}

</style>





